import { FRUIT, GARDEN, LOGIN, USER, USERQR } from "../action/type";

const initialState = {
    userQR: null,
    garden: null,
    fruit: null
}
const qrReducer = (state = initialState, action) => {
    switch (action.type) {
        case USERQR:
            state.userQR = action.payload
            return { ...state }
        case GARDEN:
            state.garden = action.payload
            return { ...state }
        case FRUIT:
            state.fruit = action.payload
            return { ...state }
        default:
            return { ...state }
    }
}
export default qrReducer;