    //                 //     `<p><b>Điện thoại </b>: ${res.data.phone} </p>  ` +
    //                 //     `${res.data.brandName == null ? `` : `<p><b>Tên thương hiệu </b>: ${res.data.brandName} </p>`}` +
    //                 //     `${res.data.productName == null ? `` : `<p><b>Tên sản phẩm </b>: ${res.data.productName} </p>`}` +
