import { LOGIN, USER } from "../action/type";

const initialState = {
    profile: {},
    login: false
}
const userReducer = (state = initialState, action) => {
    switch (action.type) {
        case USER:
            state.profile = action.payload
            return { ...state }
        case LOGIN:
            state.login = action.payload
            return { ...state }
        default:
            return { ...state }
    }
}
export default userReducer;