import React, { useEffect, useState } from 'react'
import socket from '../../Socket'
import img from '../../asset/imges/img.jpg'
import img1 from '../../asset/imges/img1.jpg'
import img2 from '../../asset/imges/img2.jpg'
import img3 from '../../asset/imges/img3.jpg'
import img4 from '../../asset/imges/img4.jpg'
import { useDispatch, useSelector } from 'react-redux'
import { createAciton } from '../../Redux/action'
import { FRUIT, GARDEN, USERQR } from '../../Redux/action/type'
import QRCode from "react-qr-code";
import { portLink } from '../../port'

function DetailQR(props) {
    const [detail, setDetail] = useState()
    const dispatch = useDispatch()
    const userQR = useSelector(state => state.qrReducer.userQR)
    const fruit = useSelector(state => state.qrReducer.fruit)
    const garden = useSelector(state => state.qrReducer.garden)
    console.log(userQR, "userQR");
    console.log(fruit, "fruit");
    console.log(garden, "garden");
    useEffect(() => {
        var data = {
            "api_passer":
            {
                "key_passer": "detailQR",
                "base58": props.match.params.qr,
            }
        }
        socket.emit('/apiBlockchainFarm', data);
        socket.on('detailQR', (res) => {
            console.log(res, "Res qr");
            if (res.user == true) {
                dispatch(createAciton(USERQR, res.data))
            } else if (res.fruit == true) {
                dispatch(createAciton(FRUIT, res.data))
            } else if (res.garden == true) {
                dispatch(createAciton(GARDEN, res.data))
            }
        })
    }, [])
    let render
    if (userQR != null) {
        render = <>
            <div style={{ flexDirection: "column", textAlign: "center" }} >
                <QRCode size={200} value={userQR.base58} />
                <p className="textQR"><b>Mã truy xuất </b>: {userQR.base58} </p>
                <p className="textQR"><b>Tên </b>: {userQR.fullName} </p>
                <p className="textQR"><b>Vai trò khách hàng </b>: {userQR.role == 1 ? "Nông dân sản xuất" : "Người dùng thường"} </p>
                <p className="textQR"><b>Điện thoại </b>: {userQR.phone} </p>
                {userQR.brandName == null ? `` : <p className="textQR"><b>Tên thương hiệu </b>:{userQR.brandName} </p>}
                {userQR.productName == null ? `` : <p className="textQR"><b>Tên sản phẩm </b>:{userQR.productName} </p>}
                {userQR.city == null ? `` : <p className="textQR"><b>Tỉnh, thành phố </b>:{userQR.city} </p>}
                {userQR.district == null ? `` : <p className="textQR"><b>Quận, huyện</b>:{userQR.district} </p>}
                {userQR.ward == null ? `` : <p className="textQR"><b>Phường, xã</b>:{userQR.ward} </p>}
                {userQR.farm != undefined ?
                    <>
                        <p className="textQR"><b>- Thông tin nông trại : </b></p>
                        <p className="textQR"><b>Tên nông trại </b> : {userQR.farm.name} </p>
                        <p className="textQR"><b>Vĩ độ </b> :{userQR.farm.longitude} </p>
                        <p className="textQR"><b>Kinh độ </b> :{userQR.farm.latitude} </p>
                        <p className="textQR"><b>Chiều dài </b> :{userQR.farm.width} (m) </p>
                        <p className="textQR"><b>Chiều rộng </b> :{userQR.farm.height} (m) </p>
                        <p className="textQR"><b>Diện tích </b> :{userQR.farm.width * userQR.farm.width} m² </p>
                    </>
                    : ``}
            </div>
        </>
    } else if (garden != null) {
        render = <>
            <div style={{ flexDirection: "column", textAlign: "center" }} >

                <QRCode size={200} value={garden.code} />
                <p className="textQR"><b>Mã truy xuất </b>: {garden.code} </p>
                <p className="textQR"><b>Tên chủ vườn </b>: {garden.name} </p>
                <p className="textQR"><b>Số lượng cây trong vườn  </b>: {garden.numberofcrops} cây </p>
                <p className="textQR"><b>Số điện thoại chủ vườn </b>: {garden.phone} </p>
                <p className="textQR"><b>Chiều dài </b>: {garden.width} m </p>
                <p className="textQR"><b>Chiều rộng </b>: {garden.height} m </p>
                <p className="textQR"><b>Diện tích </b>: {garden.height * garden.width} m² </p>
                <p className="textQR"><b>Tên cây </b>: {garden.treename} </p>
            </div>

        </>

    } else if (fruit != null) {
        render = <>
            <div style={{ flexDirection: "column", textAlign: "center" }} >
                <QRCode size={200} value={fruit.codeQR} />
                <p className="textQR"><b>Mã truy xuất </b>: {fruit.code} </p>
                <p className="textQR"><b>Tên trái </b>: {fruit.name} </p>
                <p className="textQR"><b>Ngày thu hoạch  </b>: {fruit.date} </p>
                <p className="textQR"><b>Xuất xứ </b>: {fruit.origin} </p>
                <p className="textQR"><b>Bảo quản </b>: {fruit.preservation}  </p>
                <p className="textQR"><b>Chiều rộng </b>: {fruit.height}  </p>
                <p className="textQR"><b>Quy cách </b>: {fruit.species}  </p>
                <p className="textQR"><b>Đơn vị tính </b>: {fruit.unit} </p>
                <p className="textQR"><b>Sử dụng </b>: {fruit.use} </p>
            </div>

        </>
    }
    return (

        <>
            <section>
                <div className="new-page">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-4 col-md-4">
                                {/* <div className="item-content-new">
                                    <div className="img-icon-new">
                                        <img src={img1} title="Ứng dụng blockchain vào nông nghiệp Việt Nam: Nâng cao tăng trưởng" />
                                    </div>
                                    <div className="date-time">
                                        10/11/2020
                                   </div>
                                    <div className="name-new">
                                        <a href="UNG-DUNG-BLOCKCHAIN-VAO-NONG-NGHIEP-TAI-VIET-NAM-NANG-CAO-TANG-TRUONG.html" title="Ứng dụng blockchain vào nông nghiệp Việt Nam: Nâng cao tăng trưởng">Ứng dụng blockchain vào nông nghiệp Việt Nam: Nâng cao tăng trưởng</a>
                                    </div>
                                    <div className="desc-new">
                                        Blockchain - một trong những yếu tố làm nên thành công của b                          </div>
                                    <a href="UNG-DUNG-BLOCKCHAIN-VAO-NONG-NGHIEP-TAI-VIET-NAM-NANG-CAO-TANG-TRUONG.html" title="Ứng dụng blockchain vào nông nghiệp Việt Nam: Nâng cao tăng trưởng" className="btn-see-details">Xem chi tiết</a>
                                </div> */}
                                <div className="item-content-new">
                                    {render}
                                </div>

                            </div>
                            <div className="col-lg-8 col-md-8">
                                <p style={{ boxSizing: 'border-box', marginTop: 0, marginBottom: '1rem', fontSize: 16, color: 'rgb(63, 77, 93)', fontFamily: 'Arial', textAlign: 'justify', lineHeight: '1.6 !important' }}>
                                    Thông tin từ Phòng Nông nghiệp và Phát triển nông thôn huyện Mỏ Cày Nam (tỉnh Bến Tre), trong những năm gần đây, diện tích trồng cây ăn trái của huyện phát triển khá nhanh với 1.892ha. </p>
                                <p style={{ boxSizing: 'border-box', marginTop: 0, marginBottom: '1rem', fontSize: 16, color: 'rgb(63, 77, 93)', fontFamily: 'Arial', textAlign: 'justify', lineHeight: '1.6 !important' }}>
                                    Đa phần cây ăn trái được trồng xen, tập trung nhiều nhất là cây bưởi da xanh. Bên cạnh đó, cây dừa bao phủ toàn huyện, với diện tích 16.920ha. </p>
                                <p style={{ boxSizing: 'border-box', marginTop: 0, marginBottom: '1rem', fontSize: 16, color: 'rgb(63, 77, 93)', fontFamily: 'Arial', textAlign: 'center', lineHeight: '1.6 !important' }}>
                                    <img alt src={img} style={{ boxSizing: 'border-box', verticalAlign: 'middle', borderStyle: 'none', maxWidth: '100%', width: '464.312px' }} /></p>
                                <p style={{ boxSizing: 'border-box', marginTop: 0, marginBottom: '1rem', fontSize: 16, color: 'rgb(63, 77, 93)', fontFamily: 'Arial', textAlign: 'center', lineHeight: '1.6 !important' }}>
                                    <em style={{ boxSizing: 'border-box' }}>Blockchain giúp kiểm soát, truy xuất nguồn gốc trong vườn bưởi</em></p>
                                <p style={{ boxSizing: 'border-box', marginTop: 0, marginBottom: '1rem', fontSize: 16, color: 'rgb(63, 77, 93)', fontFamily: 'Arial', textAlign: 'justify', lineHeight: '1.6 !important' }}>
                                    Huyện Mỏ Cày Nam đưa ra mục tiêu thời gian tới, toàn huyện có 30% diện tích vườn dừa thực hiện quy trình canh tác hữa cơ, trong đó có 10% được chứng nhận thực hiện liên kết tiêu thụ hiệu quả và bền vững. 70% diện tích còn lại được đầu tư thâm canh, phát triển trồng xen, nuôi xen để tăng thu nhập.</p>
                                <p style={{ boxSizing: 'border-box', marginTop: 0, marginBottom: '1rem', fontSize: 16, color: 'rgb(63, 77, 93)', fontFamily: 'Arial', textAlign: 'justify', lineHeight: '1.6 !important' }}>
                                    Cây bưởi da xanh dần trở thành cây ăn trái thế mạnh của huyện Mỏ Cày Nam. Để sản phẩm bưởi da xanh của huyện có thể phát triển bền vững, phù hợp với xu hướng canh tác dừa hữu cơ đang diễn ra trên toàn địa bàn huyện, Hội Nông dân huyện phối hợp với Trung tâm Khuyến nông tỉnh Bến Tre triển khai mô hình liên kết trồng bưởi da xanh xen trong vườn dừa theo tiêu chuẩn hữu cơ PGS tại xã Tân Trung.</p>
                                <p style={{ boxSizing: 'border-box', marginTop: 0, marginBottom: '1rem', fontSize: 16, color: 'rgb(63, 77, 93)', fontFamily: 'Arial', textAlign: 'justify', lineHeight: '1.6 !important' }}>
                                    Khi triển khai mô hình, cái khó nhất là nông dân phải thật sự “đoạn tuyệt” với phân bón vô cơ, thuốc bảo vệ thực vật, điều mà theo thói quen sản xuất, người dân vẫn sử dụng bấy lâu nay.</p>
                                <p style={{ boxSizing: 'border-box', marginTop: 0, marginBottom: '1rem', fontSize: 16, color: 'rgb(63, 77, 93)', fontFamily: 'Arial', textAlign: 'justify', lineHeight: '1.6 !important' }}>
                                    Nông nghiệp hữu cơ chú trọng tới việc tạo ra sản phẩm có chất lượng, đảm bảo các mối quan hệ công bằng giữa con người với con người, con người với thiên nhiên, với môi trường sinh thái, với vật nuôi và vạn vật có liên quan.

</p>
                            </div>

                        </div>
                    </div>
                </div>
            </section>

        </>
    )
}



export default DetailQR

