import { useSelector } from 'react-redux';
import { Switch } from 'react-router-dom';
import './App.css';
import News from './ScreensBlockchain/News';
import Blockchain from './ScreensBlockchain/Blockchain';
import Contact from './ScreensBlockchain/Contact';
import Home from './ScreensBlockchain/Home';
import Introduce from './ScreensBlockchain/Introduce';
import './scss/style.scss';
import { HomeTemplate } from './Template/HomeTemplate';
import Solution from './ScreensBlockchain/Solotion';
import DetailQR from './ScreensBlockchain/DetailQR';
import Test from './ScreensBlockchain/Test'
function App() {
  const user = useSelector(state => state.userReducer.profile)
  const login = useSelector(state => state.userReducer.login)
  const dataUser = JSON.parse(localStorage.getItem('user'))

  return (
    <Switch>
      <HomeTemplate exact path="/" Component={Home} />
      <HomeTemplate exact path="/DetailQR/:qr" Component={DetailQR} />
      <HomeTemplate exact path="/introduce" Component={Introduce} />
      <HomeTemplate exact path="/blockchain" Component={Blockchain} />
      <HomeTemplate exact path="/contact" Component={Contact} />
      <HomeTemplate exact path="/news" Component={News} />
      <HomeTemplate exact path="/solution" Component={Solution} />
      <HomeTemplate exact path="/admin" Component={Test} />
    </Switch>
  );
}
export default App;
